import * as React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
    background-color: rgba(0,0,0,0.7);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    color: #ffdf00;
`;

const Header = styled.div`
    width: 75%;
    height: 35px;
    padding: 10px 20px;
    color: #ad420a;
    font-size: 25px;
    font-weight: bolder;
    text-align: center;
    background-color: #025271;
    border-top: 3px solid #ad420a;
    border-left: 3px solid #ad420a;
    border-right: 3px solid #ad420a;
`;

const CloseButton = styled.div`
    float: right;
    margin-right: 5px;
    color: #ad420a;
    font-weight: bolder;
    font-size: 22px;
    &:hover {
        cursor: grab;
    }
`;

const Text = styled.div`
    width: 75%;
    max-height: 65%;
    min-height: 400px;
    padding: 10px 20px 20px 20px;
    background-color: #025271;
    border-left: 3px solid #ad420a;
    border-right: 3px solid #ad420a;
    font-size: 20px;
    overflow-y: scroll;
`;

const Footer = styled.div`
    width: calc(75% + 40px);
    height: 35px;
    background-color: #025271;
    border-left: 3px solid #ad420a;
    border-right: 3px solid #ad420a;
    border-bottom: 3px solid #ad420a;
`;

export interface Props{
    header: string;
    text: string;
    onClose(): void;
}

export class ModalContent extends React.Component<Props>{

    public render(): React.ReactNode{
        const { header, text, onClose } = this.props;

        return(
            <Wrapper id="modal-wrapper">
                <Header id="modal-header">
                  {header}<CloseButton onClick={onClose}>X</CloseButton>
                </Header>
                <Text id="modal-text">
                  {text}
                </Text>
                <Footer/>
            </Wrapper>
        );
    }
}