import * as React from "react";
import { observer } from "mobx-react";
import styled from "styled-components";

import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import { mediaRoute, teamRoute } from "../constants";

import { Store } from "../data/Store";
import { ArgumentsList } from "./ArgumentsList";
import { Footer } from "./Footer";
import { Navigation } from "./Navigation";
import { Media } from "./Media";
import { Team } from "./Team";

const MainBody = styled.div`
    margin: 0 8%;

    @media only screen and (max-width: 1200px){
        margin: 0;
    }
`;

export interface Props{
  store: Store;
}

@observer
export class ZashtoLev extends React.Component<Props>{

    public render() : React.ReactNode {
        const { store } = this.props;

        return (
            <MainBody>
                <BrowserRouter>
                    <Navigation/>
                    <Routes>
                        <Route path="/" element={<ArgumentsList store={store}/>} />
                        <Route path={`/${mediaRoute}`} element={<Media store={store}/>} />
                        <Route path={`/${teamRoute}`} element={<Team store={store}/>} />
                        <Route path="*" element={ <Navigate to="/" /> } />
                    </Routes>
                </BrowserRouter>
                <Footer/>
            </MainBody>
        );
    }
}
