import * as React from "react";
import styled from "styled-components";
import { bodyMainColor } from "../constants";
import { Store } from "../data/Store";
import { ArgumentPreview } from "./ArgumentPreview";
 
const Wrapper = styled.div`
    display:flex;
    flex-flow: column wrap;
    background-color: ${bodyMainColor};
    color: #ffdf00;
`;

export interface Props{
  store: Store;
}

export class ArgumentsList extends React.Component<Props>{

  public render() : React.ReactNode {
    return (
      <Wrapper>
        {
          this.props.store.arguments.map(argument => <ArgumentPreview key={argument.id} argument={argument}/>)
        }
      </Wrapper>
    );
  }
}
